.about-me__item-box-change {
  display: flex;
  width: 35px;
  height: 35px;
  margin-right: 33px;
  position: relative;
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
  border-radius: 3px;
}
.about-me__item-box-change:hover {
  border-color: #283645;
}
.about-me__item-box-change::before {
  content: "";
  width: 20px;
  height: 8px;
  border: 2px solid #d22856;
  border-top: none;
  border-right: none;
  opacity: 0;
  position: absolute;
  top: 10px;
  left: 6px;
  transition: 0.2s;
  transform: rotate(-45deg);
}
.item-box:checked + .about-me__item-box-change::before {
  opacity: 1;
}
.item-box:checked + .about-me__item-box-change {
  border-color: #d22856;
}
